header {
    background: #2f2f2f;
    position: relative;
    z-index: 10;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    & > .wrapper {
        @media (max-width:850px) {
            flex-wrap: wrap;   
        }
    }
}
nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 65%;
    padding-left: 20px;
    padding-right: 20px;
    @media (max-width:1000px) {
        min-width: 73%;
    }
    @media (max-width:850px) {
        display: none;
        order: 5;
        width: 100%;
    }
    a {
        font-size: 14px;
        color: #ffffff;
        text-decoration: none;
        &:hover,
        &.active {
            color: #ada6e8;
        }
        @media (max-width:850px) {
            display: block;
            padding: 10px 0;
            width: 100%;
            text-align: center;
        }
    }
}
.tel {
    font-size: 16px;
    text-transform: uppercase;
    color: #fff;
    text-decoration: none;
    white-space: nowrap;
    padding: 17px 0;
    display: inline-block;
    letter-spacing: 1.5px;
}

.nav {
    &_dd {
        height: 53px;
        padding-top: 19px;
        &:hover {
            & .nav_dd_in {
                display: block;
            }
        }
        & > a {
            color: #2cb3b5;
            position: relative;
            padding-right: 15px;
            display: block;
            &:after {
                content: '';
                display: block;
                width: 0px;
                height: 0px;
                border: 5px solid rgba(0,0,0,0);
                border-top-color: #2cb3b5;
                border-top-width: 4px;
                
                position: absolute;
                top: 50%;
                margin-top: 0px;
                right: 0px;
                @media (max-width:850px) {
                    display: none;
                }
            }
            &:hover {
                &:after {
                    border-top-color: #ada6e8;
                }
                
            }
        }
        &_in {
            position: absolute;
            top: 100%;
            background: #fff;
            left: 0;
            right: 0;
            box-shadow: 0px 11px 16px rgba(0, 0, 0, 0.18), inset 0px 11px 16px rgba(0, 0, 0, 0.18);
            display: none;
            @media (max-width:850px) {
                position: relative;
                margin-left: -30px;
                margin-right: -30px;
                top: -2px;
                /* bottom: 0; */
                height: 285px;
                overflow-x: auto;
            }
            .wrapper {
                align-items: flex-start;
                padding-top: 44px;
                padding-bottom: 44px;
                @media (max-width:850px) {
                    flex-wrap: wrap;
                    justify-content: center;
                    padding-bottom: 14px;
                    & > div {
                        width: 50%;
                        text-align: center;
                        margin-bottom: 30px;
                    }
                }
            }
        }
        span {
            display: block;
        }
        &_head {
            font-size: 18px;
            color: #2f2f2f;
            font-family: $bold;
            margin-bottom: 15px;
        }
        &_block {
            font-size: 18px;
            line-height: 24px;
        }
    }
}

.social {
    @media (max-width:850px) {
        flex-basis: 132px;
        text-align: right;
    }
    a {
        color: #fff;
        text-decoration: none;
        padding: 0 5px;
        font-size: 21px;
        &:hover {
            color: #ada6e8;
        }
    }
}

.mob {
    &_menu {
        display: none;
        align-items: center;
        color: #fff;
        cursor: pointer;
        @media (max-width:850px) {
            display: flex;
        }
        @media (max-width:400px) {
            width: 100%;
            order: 5;
            display: flex;
            justify-content: center;
            padding: 0 0 15px 0;
        }
        &.open {
            span {
                &:first-child {
                    margin-bottom: -4px;
                    margin-top: 4px;
                    transform: rotate(-45deg);
                    & + span {
                        opacity: 0;
                    }
                }
                &:last-child {
                    margin-top: -5px;
                    transform: rotate(45deg);
                }
            }
        }
        & > div {
            padding-top: 3px;
            margin-right: 7px;
        }
        span {
            width: 25px;
            height: 3px;
            border-radius: 10px;
            background: #fff;
            display: block;
            margin-bottom: 3px;
            
            transition: all ease 0.4s;
        }
    }
    
}
