.buy {
    font-size: 24px;
    margin-bottom: 84px;
    letter-spacing: 2.3px;
    padding-bottom: 30px;
    div {
        font-size: 36px;
            margin-top: -4px;
        & > span {
            font-family: 'SF-UI-Display-Black';
            
            position: relative;
            display: inline-block;
            padding-left: 14px;
                padding-right: 15px;
            & + div {
                display: inline-block;
            }
            & b,
            & span {
                position: relative;
                z-index: 2;
            }
            & span {
                font-size: 48px;
            }
            &:after {
                content: '';
                position: absolute;
                top: 20px;
                height: 46px;
                left: 0;
                right: 0;
                z-index: 1;
                background: #ada6e8;
            }
        }
    }
    i {
        font-style: normal;
        text-decoration: line-through;
    }
    &_big {
        box-shadow: 21px 21px 84px rgba(219, 216, 216, 0.2);
        margin-bottom: 100px;
        @media (max-width:650px) {
            flex-direction: column;
            .right,
            .left {
                padding-top: 0px;
                width: 100%;
            }
        }
        .left {
            padding-top: 40px;
        }
        .right {
            text-align: right;
            img {
                max-width: 458px;
                width: 100%;
            }
        }
        .btn {
            margin-bottom: 15px;
        }
        .buy {
            margin-bottom: 40px;
        }
    }
}

.second {
    padding-bottom: 150px;
    .h1 {
        margin-bottom: 109px;
        @media (max-width:500px) {
            margin-bottom: 30px;    
        }
    }
    &_text {
        &.right {
            padding-left: 150px;
            @media (max-width:600px) {
                padding-left: 15px;
                padding-top: 25px;
            }
        }
        div {
            position: relative;
                padding-left: 64px;
            font-size: 18px;
            margin-bottom: 25px;
            letter-spacing: 1.7px;
        }
        i {
            position: absolute;
            left: 7px;
            font-size: 27px;
            top: 50%;
            margin-top: -17px;
        }
    }
    @media (max-width:960px) {
        .lr_block {
            flex-wrap: wrap;
        }
        .left,
        .right {
            width: 100%;
        }
        .img_left {
            position: relative;
            margin: 0;
            left: 0;
            right: 0;
        }
    }
    @media (max-width:680px) {
        padding-bottom: 50px;
    }
}

.third {
    margin-bottom: 44px;
    background: url(../images/in.jpg) no-repeat center right;
    background-size: 50%;
    &_one {
        background: none;
        padding: 0!important;
    }
    .h1 {
        margin-bottom: 112px;
        @media (max-width:500px) {
            margin-bottom: 30px;
        }
    }
    .left {
        width: 57%;
    }
    @media (max-width:700px) {
        padding-bottom: 0;
        background: none;
        .left ,
        .right {
            width: 100%;
        }
        .lr_block {
            flex-wrap: wrap;
        }
    }
    @media (max-width:400px) {
        background-size: 100%;
    }
}

.sw_block {
    background: #ededed;
}

.advantages {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    flex-wrap: wrap;
    
    .nav_dd_block {
        width: 50%;
        margin-bottom: 100px;
        max-width: 260px;
        @media (max-width:900px) {
            width: 100%;
        }
        @media (max-width:560px) {
            max-width: 100%;
            margin-bottom: 20px;
        }
        
    }
}

.four {
    .h1 {
        margin-bottom: 60px;
    }
}

.five {
    padding-bottom: 60px;
}
.six {
    background: #ededed;
    padding-top: 90px;
    padding-bottom: 30px;
    position: relative;
    .nav_dd_block {
        margin-bottom: 40px;
    }
    .wrong_layout {
        .img {
            &_left {
                position: absolute;
                left: 0;
                right: 58%;
                margin-top: -0px;
                margin-left: -0px;
                    top: -18%;
                @media (max-width:1377px) {
                    top: -10%;
                }
                @media (max-width:1162px) {
                    top: -0%;
                }
                @media (max-width:934px) {
                    right: 52%;
                }
                @media (max-width:880px) {
                   top: 8%;
                }
                @media (max-width:768px) {
                   position: relative;
                }
                
            }
        }
    }
}
.six {
    @media (max-width:600px) {
        & > .wrapper {
            & > .lr_block {
                flex-direction: column;
                > .left {
                    display: block;
                    width: 100%;
                    margin-left: -10px;
                    margin-top: -133px;
                }
            }   
        }
        
    }
}
.right_main {
    width: 65%!important;
    @media (max-width:600px) {
        width: 100%!important;
        .left {
            width: 100%;
        }
    }
    .nav_dd_block {
        position: relative;
        &:before {
            content: '';
            display: block;
            right: 104%;
            left: -366px;
            height: 3px;
            background: #000;
            position: absolute;
            top: 41px;
            @media (max-width:1021px) {
               left: -300px; 
            }
            @media (max-width:768px) {
                display: none;
            }
        }
        &_list {
            &:before {
                display: none;
            }
            @media (max-width:1160px) {
                .lr_block {
                    flex-wrap: wrap;
                    .right,
                    .left {
                        width: 100%;
                    }
                }
            }
            @media (max-width:500px) {
                font-size: 16px;
            }
            
        }
        &:first-child {
            &:before {
                
            }
            & + div {
                &:before {
                    left: -277px;
                    @media (max-width:1021px) {
                       left: -230px; 
                    }
                }
            }
        }
    }
}

.seven {
    padding-top: 100px;
    padding-bottom: 100px;
    .left {
        text-align: center;
    }
    .h1 {
        margin-bottom: 60px;
    }
}

.partners {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    @media (max-width:650px) {
        justify-content: center;
        a {
            margin: 10px;
        }
    }
}

.pop_up {
    &_bg {
        display: none;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(47, 21, 57, 0.49);
        z-index: 50;
    }
    &_cont {
        display: none;
        position: fixed;
        letter-spacing: 0;
        z-index: 51;
        width: 570px;
        height: auto;
        padding: 20px 40px 60px 40px;
        background: #fff;
        top: 5%;
        left: 50%;
        margin-left: -285px;
        @media (max-width:570px) {
            left: 10px;
            right: 10px;
            padding: 20px 20px 60px 20px;
            top: 50px;
            overflow: hidden;
            overflow-x: auto;
            margin: 0;
            width: auto;
        }
        .h1 {
            margin-bottom: 30px;
            letter-spacing: 0;
             span {
                font-size: 19px;
            }
        }
        form {
            display: block;
            margin-bottom: 0;
            max-width: 350px;
            margin: 0 auto;
        }
        input,
        button {
            width: 100%;
            margin-bottom: 15px;
        }
        .lr_block {
            align-items: center;
        }
        .buy {
            margin-bottom: 0px;
            padding-bottom: 0px;
        }
        b {
            font-weight: normal;
            font-size: 30px;
        }
    }
    &_close {
        position: absolute;
        bottom: 30px;
        right: 30px;
        display: block;
        width: 40px;
        height: 40px;
        &:after,
        &:before {
            content: '';
            display: block;
            width: 30px;
            height: 3px;
            background: #000;
            transform: rotate(-45deg);
            position: absolute;
            left: 50%;
            margin-left: -15px;
            margin-top: 20px;
        }
        &:after {
            transform: rotate(45deg);
        }
    }
}
#mess {
    text-align: center;
    .h1 {
        font-family: 'SFUIDisplay-Bold';
        font-size: 40px;
    }
}
