footer {
    background: #2f2f2f;
    position: relative;
    z-index: 1;
    padding: 10px 0;
    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    & > .wrapper {
        @media (max-width:768px) {
            flex-wrap: wrap;   
        }
    }
    .nav_dd_in {
        position: absolute;
        top: auto;
        bottom: 100%;
        background: #fff;
        left: 0;
        right: 0;
        box-shadow: 0px 11px 16px rgba(0, 0, 0, 0.18), inset 0px 11px 16px rgba(0, 0, 0, 0.18);
        display: none;
    }
    @media (max-width:400px) {
        .mob_menu {
        
            display: none;
        }
        .social {
            flex-basis: 100%;
            text-align: center;
            order: 3;
        }
        nav {
            order: 0;
            display: block!important;
            text-align: center;
        }
    }
    
}