@import "variables";
@import "mixins";
html,
body {
	background: #fff;
    font-family: 'SF-UI-Display-Regular';
}
.wrapper {
    max-width: 1170px;
    margin: 0 auto;
    padding-left: 10px;
    padding-right: 10px;
}
h1 {
    font-family: 'SF-UI-Display-Light';
    font-size: 72px;
    line-height: 77px;
    font-weight: normal;
    color: #2f2f2f;
    
    letter-spacing: 18.5px;
    
        margin-bottom: 38px;
    b {
        display: block;
        font-family: 'SFUIDisplay-Bold';
        letter-spacing: 3.5px;
    }
}
h4 {
    font-size: 28px;
    font-family: 'SFUIDisplay-Bold';
        margin-bottom: 113px;
        letter-spacing: 3.2px;
}
section {
    &.first {
        margin-top: 25px;
        padding-top: 109px;
        padding-bottom: 316px;
        background: url(../images/bg_1.jpg) no-repeat top right;
        position: relative;
        @media (max-width:1300px) {
            background-position: 120% 0%;
        }
        @media (max-width:1200px) {
            background-position: 140% 0%;
        }
        @media (max-width:1100px) {
            background-position: 180% 0%;
        }
        @media (max-width:1100px) {
            background-position: 200% 0%;
        }
        @media (max-width:990px) {
            background-position: 250% 0%;
        }
        @media (max-width:950px) {
            background-position: 310% 0%;
        }
        @media (max-width:900px) {
            background: none;
            text-align: center;
                padding-bottom: 30px;
            .lr_block {
                flex-direction: column;
                .left {
                    width: 100%;
                    order: 2;
                }
                .right {
                    width: 100%;
                }
                .img_right {
                    position: relative;
                    left: auto;
                    margin-top: 10px;
                    img {
                        width: 100%; 
                    }
                }
            }
        }
        @media (max-width:700px) {
            
            h1 {
                font-size: 40px;
                line-height: 42px;
            }   
            h4 {
                font-size: 16px;
                margin-bottom: 40px;
            }
            .buy {
                margin-bottom: 30px;
            }
        }
    }
}
.btn {
    display: inline-block;
    background: #2f2f2f;
    color: #fff;
    text-decoration: none;
    font-size: 20px;
    padding: 14px 44px 17px 44px;
    letter-spacing: 2px;
    box-shadow: 5px 5px 16px #dbd8d8;
}

.lr_block {
    display: flex;
    justify-content: center;
        align-items: stretch;
    .right,
    .left {
        width: 50%;
    }
}
.wrong {
    &_layout {
        .img {
            &_right {
                position: absolute;
                right: 0;
                left: 42.3%;
                margin-top: -102px;
            }
            &_left {
                position: absolute;
                left: 0;
                right: 41%;
                margin-top: -20px;
                margin-left: -29px;
                img {
                    width: 100%;
                }
            }
        }
    }
}

.h1 {
    color: #2f2f2f;
    text-align: center;
    font-size: 40px;
    letter-spacing: 3.5px;
    @media (max-width:500px) {
        font-size: 30px;
    }
    span {
        display: block;
        font-size: 24px;
        @media (max-width:500px) {
            font-size: 18px;
        }
    }
}

input,
button {
    background: #ededed;
    border: none;
    width: 30%;
    color: #2f2f2f;
    font-size: 20px;
    padding: 20px 26px;
    border: 1px solid #ededed;
    @include plhl {
        color: #2f2f2f;
    }
    @media (max-width:650px) {
        width: 100%;   
        margin-bottom: 10px;
    }
}
form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 95px;
    @media (max-width:650px) {
        max-width: 400px;
        margin: 0 auto 50px auto;
        flex-direction: column;
    }
}
button {
    background: #37a7ac;
    color: #fff;
    box-shadow: 5px 5px 16px #dbd8d8;
}

.center {
    text-align: center;
}
.hide_img {
    display: none;
    img {
        width: 100%;
    }
    @media (max-width:700px) {
        display: block;
        margin-right: -10px;
    }
}
.img_right {
                    img {
                        width: 100%; 
                    }
                }
